<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import vueAmx from "vue-amx";
import Vue from "vue";
import { setTimeout } from 'timers';
let appNet = {
  name: "appNet",
  type: false,
  store: {
    type: "none"
  }
};
let store = [appNet];
Vue.use(vueAmx, store);
export default {
  name: "App",
  created() {
    try {
      setTimeout(item => {
        document.querySelector(".box").style.opacity = "0";
        setTimeout(()=>{
          document.querySelector(".box").style.display = "none";
        },2000)
      }, 3000)

    } catch (e) {
      
    }
  }
};
</script>

<style>
* {
  padding: 0;
  margin: 0;
}
</style>



