        <img src="../assets/headerc/header-logo-dark.svg" alt srcset />
        <div class="reservebtn" @click="reservation(1)" style="cursor: pointer;"> M-Byte</div>
      </div>
    </div>
    <img id="image" style="width:100vw;" src alt />
  </div>
</template>
<script>
