<style scoped>
/* common use */

section {
  padding-top: 14vh;
}

h1 {
  font-size: 2rem;
  color: white;
  line-height: 16vw;
  font-weight: bold;
}

h2 {
  color: #000;
  font-size: 1.75rem;
  font-weight: normal;
}
.desc {
  font-size: 1.25rem;
  font-weight: lighter;
  margin-top: 3vh;
}

h3 {
  color: #000;
  font-size: 8vw;
  font-weight: normal;
}
h4 {
  color: #000;
  font-size: 1.4rem;
  font-weight: bold;
}

.slider-desc {
  margin-top: 1.14rem;
  margin-top: 5vh;
}

.slider-desc h4 {
  margin-bottom: 2vh;
}

.slider-desc p {
  font-size: 4vw;
  line-height: 1.6rem;
  color: #636363;
}

.slider {
  margin-top: 5vh;
}

.slider .swiper-container {
  width: 100vw;
}

.slider >>> .swiper-pagination-bullet {
  background: #525252;
  width: 10px;
  height: 10px;
}
.slider >>> .swiper-pagination-bullet-active {
  background: #252525;
}

.swiper-pagination {
  position: relative !important;
  margin-top: 6vh;
}

.slider img {
  width: 100vw;
  /* height: 30vh; */
}

/* .swiper-slide,
.swiper-wrapper {
  margin-bottom: 8vh !important;
} */

/* first page */

.top {
  background-image: url("../assets/headerc/home.png");
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100vw;
  color: #ffffff;
}

.top .logo {
  width: 30vw;
  margin-top: 14vh;
}

.top .slogan {
  margin-top: 10vh;
}

.top .slogan h1 {
  line-height: 1.6;
}

.top .slogan h1:first-child {
  letter-spacing: 0.16rem;
}

.top .buttom {
  border: 1px solid rgba(255, 255, 255, 0.8);
  line-height: 9vw;
  width: 35vw;
  margin-top: 15vh;
}

.button1 {
  margin: 14vw auto 0;
  width: 35vw;
  height: 10vw;
}

/* second page */

#video2 {
  width: 100vw;
  margin-top: 5vh;
  object-fit: cover;
}

.buttom4 {
  position: fixed;
  width: 100vw;
  background-color: #ffffff;
  bottom: -1px;
  left: 0px;
  z-index: 2;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  padding: 2vw 0px;
  font-size: 4vw;
  box-shadow: 0px -6vw 17vw rgba(0, 0, 0, 0.1);
  animation-duration: 1s !important;
}

.buttom4 > img {
  height: 6vw;
  margin-left: 2vw;
}

.reservebtn {
  display: inline-block;
  line-height: 6vw;
  background-color: #ba6954;
  padding: 1.5vw 6vw;
  margin-right: 2vw;
}

.animated {
  animation-duration: 2s;
}
.delay-z5s {
  animation-delay: 0.5s;
}

.arrow {
  position: absolute;
  bottom: 8vw;
  left: 46vw;
  width: 8vw;
}

/* price-table */

.price-table {
  background-color: #d7d7d7;
  padding: 6vh 4vw;
  text-align: left !important;
  margin-top: 10vh;
}

.price-box {
  margin-bottom: 5vh;
}
.price-table h4 {
  padding-left: 4vw;
}
.price-table .header {
  font-size: 1.14rem;
  line-height: 4vh;
  border-bottom: 1px solid #b9b9b9;
  display: block;
  padding-left: 4vw;
  margin-top: 3vh;
  padding-bottom: 1vh;
}

.price-table .item div {
  line-height: 2;
  padding-left: 4vw;
  font-size: 1.14rem;
  margin: 0.6vh 0;
  color: #333;
}

.item-highlight {
  /* background-color: #e7e7e7; */
  background-color: #d0d0d0;
}

/* last page */

.last-page {
  background-image: url("../assets/headerc/footer.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.last-page .desc {
  margin-top: 4vh;
  font-size: 1.6rem;
}

.last-page h3 {
  padding-top: 14vh;
}

.last-page .buttom3 {
  position: absolute;
  bottom: 10vh;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100vw;
}

.last-page .buttom3 .ioc {
  width: 4vw;
  margin-left: 4vw;
}
</style>



